import React, { useContext, useEffect, useState } from 'react';
import HomePage from './home-page';
import { UserContext } from '../../App';
import { httpFetch } from '../../../services/http-fetch';
import { baseUrl } from '../../../services/base-url';
import { FetchData } from '../../../gateways/http-fetch/http-fetch-response';

const HomePageContainer = () => {
  const { user } = useContext(UserContext);
  const [organization, setOrganization] = useState<FetchData>({ attributes: {} });
  useEffect(() => {
    if (user.login === '') {
      location.reload();
    }
    httpFetch
      .get(`${baseUrl}/organizations/${user.organizations[0]}?actual_balance=true`)
      .then(async data => {
        if (data) {
          await setOrganization(data.data);
        }
      });
  }, []);
  const checkBalance = (balance: number, off?: boolean) => {
    if (off) {
      switch (true) {
        case Math.sign(Number(balance)) === 0:
          return false;
        case Math.sign(Number(balance)) === -0:
          return false;
        case Math.sign(Number(balance)) === 1:
          return true;
        case Math.sign(Number(balance)) === -1:
          return false;
      }
    } else {
      return true;
    }
    return false;
  };
  return (
    <HomePage
      organization={organization ? organization.attributes : { ogrn: 0 }}
      user={user}
      checkBalance={checkBalance}
    />
  );
};

export default HomePageContainer;
