import React from 'react';
import './home-page.css';
import { createMuiTheme, MuiThemeProvider, Typography } from '@material-ui/core';
import { green } from '@material-ui/core/colors';
import { Organization } from '../../../types/organization';
import { User } from '../../hooks/user';
import joinCssClasses from '../../../utils/css';
import { Button } from '../common/button/button';
import Card from '../common/card/card';
import UserIcon from '../../../assets/icons/user.svg';
import NavigationHeader from '../common/navigation-header/navigation-header';

const theme = createMuiTheme({
  palette: {
    secondary: green,
  },
});

type Props = {
  organization?: Organization;
  user?: User;
  checkBalance: (balance: number, off?: boolean) => boolean;
};

const HomePage = ({ organization, user, checkBalance }: Props) => {
  const chekedBalance = checkBalance(
    organization && organization.balance ? organization.balance : 0,
    organization && organization.sett_org_billing,
  );

  const chekedDealerBalance = checkBalance(
    organization && organization.dealer_balance ? organization.dealer_balance : 0,
    true,
  );
  return (
    <>
      {organization && organization.name && user && (
        <div className="home">
          <NavigationHeader>Главная</NavigationHeader>
          <div className="name-wrapper">
            <img src={UserIcon} alt="name" className="mr-3" />
            <Typography className="name">
              {user.last_name} {user.first_name[0]}. {user.middle_name[0]}.
            </Typography>
          </div>

          {organization.sett_org_billing && (
            <Card
              id="balance-field"
              className={joinCssClasses('mb-3', chekedBalance ? '' : 'negative-balance')}
              label="Баланс"
              color="gray"
              helperText={chekedBalance ? '' : 'Пополните баланс!'}
            >
              {`${organization.balance}р`}
            </Card>
          )}
          {!chekedDealerBalance && (
            <Typography style={{ margin: '0 0 20px 0' }} color="secondary">
              Услуга недоступна, обратитесь в офис продаж
            </Typography>
          )}
          <Card id="organization-field" className="mb-3" label="Организация">
            {organization.name}
          </Card>
          <Card id="organization-number-field" className="mb-3" label="ОГРН">
            {organization.ogrn}
          </Card>

          <Card id="kis-art-field" className="mb-3" label="КИС АРТ id">
            {user.kis_art_id}
          </Card>

          <div className="mb-3">
            <Button color="primary" component="a" href="/medicallist">
              Мои медосмотры
            </Button>
          </div>

          <div className="mb-3">
            <Button color="primary" component="a" href="/technicalinspectionslist">
              Мои техосмотры
            </Button>
          </div>

          <div className="mb-3">
            <Button color="primary" component="a" href="/waybillslist">
              Мои путевые листы
            </Button>
          </div>

          <div className="mb-3">
            <MuiThemeProvider theme={theme}>
              <Button
                color="secondary"
                href="/newinspection"
                disabled={!chekedBalance || !chekedDealerBalance}
                component="a"
              >
                Новый осмотр
              </Button>
            </MuiThemeProvider>
          </div>

          <div className="mb-6">
            <MuiThemeProvider theme={theme}>
              <Button
                color="secondary"
                href="/newmedinspection"
                disabled={!chekedBalance || !chekedDealerBalance}
                component="a"
              >
                Только медосмотр
              </Button>
            </MuiThemeProvider>
          </div>
        </div>
      )}
    </>
  );
};

export default HomePage;
