import React from 'react';
import './navigation-header.css';
import arrow from '../../../../assets/icons/back-arrow.svg';
import Delimiter from '../delimiter/delimiter';
import { Button, Typography } from '@material-ui/core';
import { Computer } from '@material-ui/icons';
import { useReactPWAInstall } from 'react-pwa-install';
import myLogo from '../../../../assets/icons/logo.png';

type Props = {
  children: React.ReactNode;
};

const NavigationHeader = ({ children }: Props) => {
  const { pwaInstall, supported, isInstalled } = useReactPWAInstall();
  const location = window.location.pathname;

  const handleClick = () => {
    pwaInstall({
      title: 'Установить приложение',
      logo: myLogo,
      description: 'Telemedic App',
    }).then(() => alert('Приложение установленно или инструкция по установке прочитана'));
  };
  return (
    <div className="navigation-header">
      <nav className="navigation-header-wrapper">
        <div
          style={{
            height: '100px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          {location !== '/home' && (
            <img
              className="navigation-header-image"
              src={arrow}
              alt="arrow"
              onClick={() => {
                window.history.back();
              }}
            />
          )}
        </div>
        <div className="navigation-header-content">
          <Typography>{children}</Typography>
        </div>
        {supported() && !isInstalled() && (
          <Button onClick={handleClick}>
            <Computer />
          </Button>
        )}
      </nav>
      <Delimiter />
    </div>
  );
};

export default NavigationHeader;
