import React from 'react';
import './card.css';

import joinCssClasses from '../../../../utils/css';
import Typography from '../typography/typography';

type Props = {
  className?: string;
  children?: React.ReactNode;
  label?: string;
  id?: string;
  color?: 'gray' | 'white';
  helperText?: string;
};

const Card = ({ children, label, helperText, id, className, color = 'gray' }: Props) => {
  return (
    <>
      {children && (
        <div className={joinCssClasses('card', className)}>
          <label className={color === 'gray' ? 'card__label' : 'card__label-white'}>{label}</label>
          <div id={id} className={joinCssClasses('card__wrapper', color)}>
            <Typography className="card__text">{children}</Typography>
          </div>
          <Typography className="helperText">{helperText}</Typography>
        </div>
      )}
    </>
  );
};

export default Card;
