import React from 'react';
import './button.css';
import joinCssClasses from '../../../../utils/css';
import Typography from '../typography/typography';

type Props = React.DetailedHTMLProps<
  React.ButtonHTMLAttributes<HTMLButtonElement>,
  HTMLButtonElement
> & { component?: 'div' | 'button' | 'input' | 'a'; href?: string };

export const Button = (props: Props) => {
  const {
    children,
    className,
    component: Component = 'button',
    href,
    color,
    ...otherProps
  } = props;

  const handleClick = (e: React.MouseEvent<HTMLElement>) => {
    e.preventDefault();
    if (href) {
      document.location.href = href;
    }
  };

  const content =
    typeof children === 'string' ? (
      <Typography className="button__title">{children}</Typography>
    ) : (
      children
    );
  const joinedClassName = joinCssClasses('button', color, className);
  if (Component === 'a') {
    return (
      <a className={joinedClassName} href={href} onClick={handleClick}>
        {content}
      </a>
    );
  }

  if (Component !== 'button') {
    return <Component className={joinedClassName}>{content}</Component>;
  }

  return (
    <button type="button" className={joinedClassName} {...otherProps}>
      {content}
    </button>
  );
};
